

.inputWrap input{
  /* border: 0px; */
  padding: 0;
  margin: 0;
  text-transform: uppercase;
}
.inputWrap input[type="text"]{
  box-sizing: border-box;
}
.inputWrap input::placeholder{
  text-transform: none;
}
.inputWrap{
  width: 200px;
  margin-right: 20px;
  margin-bottom: 5px;
}
.inputWrap label{
  font-weight: bold;
}
.inputWrap select{
  /* border: 0px; */
  padding: 0px;
  margin: 0px;
}

.formDiv{
  background-color: #fff;
  min-width:257px;
  border: 2px solid #888888;
  border-radius: 0.5%;
  font-size: 12.5px;
  padding: 15px;
}

.fsublock{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  padding: 2px; 
  
}
.fsu{
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  flex: 0 1 420px;
}
.fsuh4{
  flex: 0 1 auto;
}
.fsublockmini{
  display: flex;
  flex-wrap: wrap;
  flex: 1 1 auto;
}
.fsublockoption{
  font-family: 'MuseoSansCyrl', 'serif';
  flex: 0 0 200px;
  border: 3px solid #888888;
  background-color: rgb(255, 255, 255);
}
.infoblock{
  display: flex;
  flex-wrap: wrap;
  min-width:257px;
  border: 3px solid #888888;
  border-radius: 0.5%;
  padding-left: 1%;
  min-height: 55px;
  justify-content: flex-start;
  align-items: flex-end;
  margin-right: 20px;
}
.infoblockDiv{
  width: 200px;
  height: 22px;
  display: flex;
  flex-direction: column;
  border: 2px solid #888888;
}

.infoblockQuantity{
  display: flex;
  flex-direction: column;
  min-width:257px;
  border: 3px solid #888888;
  border-radius: 0.5%;
  padding-left: 1%;
  min-height: 55px;
  justify-content: flex-end;
  align-items: flex-start;
  margin-right: 20px;
  
}

.infoblockQuantityDetail{
  display: flex;
  flex-wrap: wrap;
  max-width:175vh;
  min-height: 55px;
  justify-content: flex-start;
  align-items: flex-end;
  
}
.awb{
  padding-left: 1.5%;

}
.detailAndTotal{
  border: 2px solid;
  border-radius: 0.5%;
  width:175vh;
  padding-left: 1%;
  margin-left: 2vh;
  height: 14vh;
}
.detail{
  display: grid;
  align-items: center;
  grid-template-columns: repeat(7, 1fr);
  
}
.total{
  display: grid;
  align-items: center;
  grid-template-columns: repeat(7, 1fr);
}
h4{
  margin: 0;
  padding: 0.5%;
  font-size: 15px;
}
.MovementDetail{
  border: 2px solid;
  border-radius: 0.5%;
  width:175vh;
  padding-left: 1%;
  margin-left: 2vh;
  height: 27.5vh;
}
.movement{
  display: grid;
  align-items: center;
 
  grid-template-columns: repeat(7, 1fr);

}
.time{
  display: grid;
  align-items: center;
 
  grid-template-columns: repeat(7, 1fr);

}

.validate{
  border: 2px solid;
  border-radius: 0.5%;
  min-width:300px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 1%;
  height: 9.5vh;
  color:gray;
  white-space: pre-line
}

.validateMessage{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.message{
  margin-left:0.25%;
  width: 200px;
  height: 30px;
  font-weight: bold;
  font-size: 15px;
  padding: 10px 25px;
  margin-bottom: 20px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: black;
  background-color: #dadada;
  border: none;
  border-radius: 5px;
  box-shadow: 0 4px #aeaeae;
}

.message:hover{
  box-shadow: 0px 0px 5px 1px #4a4a4a;
  background-color: #1d49aa;
}
.message:focus{
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}
