.publish{
  display: flex;
  justify-content: flex-end;
}

.errorValidation{
  bottom: 0px;
  color: rgb(255, 0, 0);
  font-size: 10px;
  line-height: 10px;
  padding: 2px 0px;
  margin: 0;
}
#received-from-block{
  min-width: 400px;
}
#received-from-input{
  font-size: 10px;
  width: 400px;
}
