@font-face {
  font-family: 'MuseoSansCyrl';
  font-weight: 100;
  src: local('MuseoSansCyrl'), url(./components/fonts/MuseoSansCyrl.otf) format('opentype')
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-weight: 300;
  src: local('MuseoSansCyrl'), url(./components/fonts/MuseoSansCyrl_0.otf) format('opentype')
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-weight: 500;
  src: local('MuseoSansCyrl'), url(./components/fonts/MuseoSansCyrl_1.otf) format('opentype')
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-weight: 700;
  src: local('MuseoSansCyrl'), url(./components/fonts/MuseoSansCyrl_2.otf) format('opentype')
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-weight: 900;
  src: local('MuseoSansCyrl'), url(./components/fonts/MuseoSansCyrl_3.otf) format('opentype')
}

body{
  font-family: 'MuseoSansCyrl', serif;
  padding: 0;
  margin: 0;
  background-color: #f5f5f5;
  display: flex;
  justify-content: center
}
html, body{
  min-height: 100%;
  max-width: 100%;
  font-size: 100%;
  line-height: 1;

}

#root{
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 30px 20px 20px 20px;

}

.logo{
  height: 1.2cm;
  padding: 0.2cm 0 0.3cm 0cm;
  width: 207px;
  height: 45px; 
}